.container {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.title {
  align-self: flex-start;
}

.table {
  display: flex;
  max-width: 520px;
  width: 100%;
  flex-direction: row;
  gap: 20px;
  list-style: none;
  padding-left: 0;
  margin: 0;
}

.list {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
  flex: 1 1 auto;
}
